@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500;700&display=swap);
.game-card-reactions {
    box-sizing: border-box;
    background-color: #8c00ff;
    padding: 1.5mm;
    border: 1.5mm solid rgba(0, 0, 0, 0.6);
    width: 45mm;
    height: 65mm;
    border-radius: 3mm;
    position: relative;
    margin: 0.5mm;
    color: hsla(200, 100%, 100%, 100%)
  }
  .game-card-reactions:hover{
    transform: scale(2);
    z-index: 1;
  }
  
  .game-card-reactions > div {
    background-color: #0d81ff;
    position: absolute;
    height: 7mm;
    box-sizing: border-box;
    border: 0.5mm solid rgba(0, 0, 0, 0.6);
    border-radius: 3mm;
    padding: 1mm;
    line-height: 4mm;
    font-size: 4mm;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    justify-content: space-between;
    box-shadow: 0.5mm 0.5mm rgba(0, 0, 0, 0.2);
    
    font-weight: bold;
  }
  
  .game-card-reactions > div > label {
    flex: 2 1;
  }
  
  .game-card-reactions > div > div {
    flex: 1 1;
  }
  
  .game-card-reactions .name {
    width: calc(100% - 3mm);
    display: block;
    text-align: center;
    border-radius: 2mm;
  }
  .game-card-reactions .green {
    background-color: #19FF67;
    top: 15%;
    left: 3%;
    line-height: 7mm;
    font-size: 6mm;
    height: 65%;
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    
  }
  .game-card-reactions .yellow {
    background-color: #FFAC1C;
    display: flex;
    left: 15%;
    bottom: 5%;
    line-height: 7mm;
    font-size: 6mm;
    height: 15%;
    width: 70%;
    justify-content: center;
    
  }
  .game-card-reactions .red {
    background-color: #FC0E08;
    top: 15%;
    right: 3%;
    line-height: 7mm;
    font-size: 6mm;
    height: 65%;
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    
  }

  .game-card-reactions .textBox{
    background-color: #0d81ff;
    color: black;
    right: 20%;
    top:25%;
    line-height: 7mm;
    font-size: 3mm;
    height: 50%;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  
  .game-card-reactions .discard {
    right: 1.5mm;
    bottom: 1.5mm;
    background-color: red;
  }
  
.game-card-topics {
    box-sizing: border-box;
    background-color:  #8c00ff;
    padding: 1.5mm;
    border: 1.5mm solid rgba(0, 0, 0, 0.6);
    width: 45mm;
    height: 65mm;
    border-radius: 3mm;
    position: relative;
    margin: 0.5mm;
    color: hsla(200, 100%, 100%, 100%)
  }
  
  .game-card-topics > div {
    background-color: #0d81ff;
    position: relative;
    height: 7mm;
    box-sizing: border-box;
    border: 0.5mm solid rgba(0, 0, 0, 0.6);
    border-radius: 3mm;
    padding: 1mm;
    line-height: 4mm;
    font-size: 4mm;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    justify-content: space-between;
    box-shadow: 0.5mm 0.5mm rgba(0, 0, 0, 0.2);
    text-shadow: 0.3mm 0.3mm rgba(0, 0, 0, 0.6);
    font-weight: bold;
  }
  
  .game-card-topics > div > label {
    flex: 2 1;
  }
  
  .game-card-topics > div > div {
    flex: 1 1;
  }
  
  .game-card-topics .name {
    width: calc(100% - 3mm);
    display: block;
    text-align: center;
    border-radius: 2mm;
  }
  .game-card-topics .dificulty{
    position: absolute;
    padding-bottom: 10mm;
    height: 20mm;
    background-color: #8c00ff;
    align-self: flex-end;
    display: grid;
    grid-gap: 1mm;
    gap: 1mm;
    grid-template-columns: 1fr 1fr 1fr;
    bottom: 3mm;
  }
  .game-card-topics .greenCard {

    height: 64px;
    width: 42px;
    border-radius: 10%;
    border: 0.5mm solid rgba(0, 0, 0, 0.6);
    background-color: #19FF67
    
  }
  .game-card-topics .yellowCard {
    height: 64px;
    width: 42px;
    border-radius: 10%;
    border: 0.5mm solid rgba(0, 0, 0, 0.6);
    background-color: #FFAC1C
  }
  .game-card-topics .redCard {
    height: 64px;
    width: 42px;
    border-radius: 10%;
    border: 0.5mm solid rgba(0, 0, 0, 0.6);
    background-color: #FC0E08
    
  }

  
  .game-card-topics .discard {
    right: 1.5mm;
    bottom: 1.5mm;
    background-color: blue;
  }
  
.player-board {
  padding: 2mm;
  margin: 0.5mm;
  background-color: hsla(120, 53%, 79%, 50%);
  border: 2mm solid hsla(0, 0%, 0%, 60%);
  border-radius: 2mm;
  display: flex;
  flex-wrap: wrap;
}
.player-board .passButton{
  right: 1.5mm;
  bottom: 1.5mm;
  background-color: lightblue;
}
.player-board h1 {
  font-size: 5mm;
  font-size: 5mm;
}
.player-board > h1,
.player-board > .player-hand {
  width: 100%;
}
.player-board > .player-admnistration{
  width: 100%;
}
.player-board > .player-discard{
  width: 100%;
}
.player-board .redBoard{
  display: flex;
  background-color: #FC0E08;
  width: 100%;
}
.player-board .yellowBoard{
  display: flex;
  background-color: #FFAC1C;
}
.player-board .greenBoard{
  display: flex;
  background-color: #19FF67;
}


.player-hand > div,
.player-admnistration > div,
.player-deck  > div,
.player-discard  > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.player-deck {
  opacity: 0.7;
}
.player-deck:hover {
  opacity: 1;
}

.game-board {
  font-family: Rubik, Arial, Helvetica, sans-serif;
  font-size: 14px;
  -webkit-font-smoothing: antialised;
  max-width: calc(1.1 * 6 * 45mm);
  width: 100%;
  margin: 0;
  padding: 3mm;
  border: 3mm solid hsla(120, 100%, 10%, 100%);
  background-color: #a43cff;
  border-radius: 3mm;
  zoom: 0.6;
}

.card-offer,
.player-boards {
  padding: 3mm;
  border: 3mm dotted hsla(120, 100%, 10%, 30%);
  border-radius: 3mm;
  margin-bottom: 3mm;
}
.card-offer .card-offer-cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card-offer h1,
.player-boards h1 {
    color: hsla(120, 100%, 10%, 100%);
    margin: 0mm 0mm 3mm 0mm;
    
}
